   .diagnose-wrap .diagnose-main .optimiz-box .circle.box1 p {\n            font-size: 0.3rem; }\n            .diagnose-wrap .diagnose-main .optimiz-box .circle.box1 p.time {\n              opacity: .45; }\n        .diagnose-wrap .diagnose-main .optimiz-box .circle.box2 {\n          background: #417d7d;\n          animation: animat2 2s infinite;\n          -webkit-animation: animat2 1s infinite;\n          z-index: 9; }\n        .diagnose-wrap .diagnose-main .optimiz-box .circle.box3 {\n          background: #1c6371;\n          animation: animat3 2s infinite;\n          -webkit-animation: animat3 1s infinite;\n          z-index: 8; }\n\n@keyframes animat2 {\n  0% {\n    transform: scale(1); }\n  100% {\n    transform: scale(1.2);\n    opacity: 0; } }\n\n@-webkit-keyframes animat2 {\n  0% {\n    transform: scale(1); }\n  100% {\n    transform: scale(1.2);\n    opacity: 0; } }\n\n@keyframes animat3 {\n  0% {\n    transform: scale(1); }\n  100% {\n    transform: scale(1.4);\n    opacity: 0; } }\n\n@-webkit-keyframes animat3 {\n  0% {\n    transform: scale(1); }\n  100% {\n    transform: scale(1.4);\n    opacity: 0; } }\n  .diagnose-wrap .bottom {\n    width: 100%;\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    height: 2.36rem;\n    background: url(\"../../assets/img/diagnose_bottom.png\");\n    background-size: 100%;\n    z-index: 9; }\n  .diagnose-wrap .pop-wrap .form-container .top {\n    padding-bottom: .3rem;\n    overflow: hidden; }\n    .diagnose-wrap .pop-wrap .form-container .top .close {\n      font-size: .32rem;\n      color: #d9d9d9; }\n  .diagnose-wrap .pop-wrap .form-container .question-list {\n    overflow: hidden;\n    width: 100%;\n    margin-bottom: .6rem; }\n    .diagnose-wrap .pop-wrap .form-container .question-list .list {\n      float: left;\n      width: 25%;\n      padding-right: .1rem; }\n      .diagnose-wrap .pop-wrap .form-container .question-list .list:nth-child(4n) {\n        padding-right: 0; }\n      .diagnose-wrap .pop-wrap .form-container .question-list .list span {\n        display: block;\n        line-height: .6rem;\n        background: #d8e8f8;\n        border-radius: 3px;\n        color: #558ec1;\n        text-align: center;\n        margin-bottom: .1rem; }\n        .diagnose-wrap .pop-wrap .form-container .question-list .list span.on {\n          background: #558ec1;\n          color: #fff; }\n  .diagnose-wrap .pop-wrap .form-container .text {\n    position: relative;\n    margin-bottom: .6rem; }\n    .diagnose-wrap .pop-wrap .form-container .text textarea {\n      padding: .15rem;\n      width: 100%;\n      height: 1.14rem;\n      background: #f6f6f6;\n      border: 1px solid #e6e6e6;\n      border-radius: 4px; }\n    .diagnose-wrap .pop-wrap .form-container .text .tip {\n      position: absolute;\n      right: .15rem;\n      bottom: .1rem;\n      font-size: 0.18rem;\n      color: #c7c7c7; }\n  .diagnose-wrap .pop-wrap .form-container .input-box {\n    width: 100%;\n    margin-bottom: .2rem; }\n    .diagnose-wrap .pop-wrap .form-container .input-box input {\n      width: 100%;\n      border-radius: 4px;\n      text-align: center;\n      line-height: .24rem;\n      padding: .18rem 0;\n      border: 1px solid #d2d2d2;\n      background: #fff;\n      color: #333; }\n      .diagnose-wrap .pop-wrap .form-container .input-box input.bg-gray {\n        background: #f6f6f6; }\n    .diagnose-wrap .pop-wrap .form-container .input-box.disabled input {\n      background: #f3f3f3;\n      cursor: not-allowed; }\n  .diagnose-wrap .pop-wrap .form-container .recomand {\n    font-size: .2rem;\n    color: #9e9e9e;\n    text-align: center; }\n  .diagnose-wrap .pop-wrap .form-container .submit {\n    margin: .6rem 0; }\n  .diagnose-wrap .loading {\n    width: 1.16rem;\n    height: 1.16rem;\n    position: relative;\n    display: inline-block;\n    margin-right: .1rem;\n    border: 3px solid #f39800;\n    border-radius: 50%;\n    text-align: center;\n    line-height: 1rem; }\n  .diagnose-wrap .loading .progress {\n    position: absolute;\n    width: 1rem;\n    height: 1rem;\n    background-color: #035165;\n    border-radius: 50%;\n    left: .08rem;\n    top: .08rem;\n    line-height: 1rem;\n    text-align: center; }\n  .diagnose-wrap .left-circle,\n  .diagnose-wrap .right-circle {\n    width: .58rem;\n    height: 1.16rem;\n    overflow: hidden;\n    position: relative;\n    float: left; }\n  .diagnose-wrap .left-circle {\n    border-radius: 1.16rem 0 0 1.16rem; }\n  .diagnose-wrap .right-circle {\n    border-radius: 0 1.16rem 1.16rem 0; }\n  .diagnose-wrap .left-circle:after,\n  .diagnose-wrap .right-circle:after {\n    content: \"\";\n    position: absolute;\n    display: block;\n    width: .58rem;\n    height: 1.16rem;\n    border-radius: 1.16rem 0 0 1.16rem;\n    background-color: #f39800; }\n  .diagnose-wrap .right-circle:after {\n    content: \"\";\n    position: absolute;\n    display: block;\n    border-radius: 0 1.16rem 1.16rem 0; }\n\n@-webkit-keyframes rotateB {\n  25%,\n  50% {\n    transform: rotateZ(180deg); }\n  75%,\n  100% {\n    transform: rotateZ(360deg); } }\n\n@-webkit-keyframes rotateA {\n  0%,\n  25% {\n    transform: rotateZ(0deg); }\n  50%,\n  75% {\n    transform: rotateZ(180deg); }\n  100% {\n    transform: rotateZ(360deg); } }\n\n@keyframes rotateB {\n  25%,\n  50% {\n    transform: rotateZ(180deg); }\n  75%,\n  100% {\n    transform: rotateZ(360deg); } }\n\n@keyframes rotateA {\n  0%,\n  25% {\n    transform: rotateZ(0deg); }\n  50%,\n  75% {\n    transform: rotateZ(180deg); }\n  100% {\n    transform: rotateZ(360deg); } }\n  .diagnose-wrap .left-circle:after {\n    -webkit-animation: rotateA 2s infinite linear;\n    animation: rotateA 2s infinite linear;\n    transform-origin: right center; }\n  .diagnose-wrap .right-circle:after {\n    -webkit-animation: rotateB 2s infinite linear;\n    animation: rotateB 2s infinite linear;\n    transform-origin: left center;\n    -webkit-transform-origin: left center; }\n  .diagnose-wrap .circle2 .left-circle:after {\n    -webkit-animation: rotateA 2s .5s infinite linear;\n    animation: rotateA 2s .5s infinite linear; }\n  .diagnose-wrap .circle2 .right-circle:after {\n    -webkit-animation: rotateB 2s .5s infinite linear;\n    animation: rotateB 2s .5s infinite linear; }\n  .diagnose-wrap .circle3 .left-circle:after {\n    -webkit-animation: rotateA 2s .8s infinite linear;\n    animation: rotateA 2s .8s infinite linear; }\n  .diagnose-wrap .circle3 .right-circle:after {\n    -webkit-animation: rotateB 2s .8s infinite linear;\n    animation: rotateB 2s .8s infinite linear; }\n","@charset \"utf-8\";\n/*\n@module 颜色\n*/\n\n  /*背景色*/\n\n  /*背景颜色名*/\n  $bg-blue:#3a8dda;//蓝色背景\n  /*边框颜色*/\n  $border-gray:#d9d9d9;//灰色边框\n  $border-blue:#3a8dda;//蓝色边框\n  /*字体颜色*/\n$font-baseColor:#fff;//基础字体颜色\n$font-greenColor:#138782;//菜单选中颜色\n$font-blackColor:#353535;\n$font-grayBlueColor:#546a79;//灰蓝色\n$font-grayColor:#a6afb6;//灰色\n$font-yellowColor:#faba32;//黄色\n$font-blueColor:#3a8dda;//蓝色\n$font-redColor:#e52815;//红色\n/*\n@module 字体大小\n*/\n$fontSizeBase:.24rem;\n$fontSize18:.18rem;\n$fontSize20:.2rem;\n$fontSize26:.26rem;\n$fontSize28:.28rem;\n$fontSize30:.30rem;\n$fontSize40:.40rem;\n","@import 'variate.scss';\n\n/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */\n\nhtml {\n    min-height: 100%;\n    -webkit-text-size-adjust: 100%;\n    -ms-text-size-adjust: 100%;\n    -webkit-overflow-scrolling: touch;\n    padding: constant(safe-area-inset-top) constant(safe-area-inset-right) constant(safe-area-inset-bottom) constant(safe-area-inset-left);\n}\n\n\n/* 内外边距通常让各个浏览器样式的表现位置不同 */\n\nbody,\ndiv,\ndl,\ndt,\ndd,\nul,\nol,\nli,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\npre,\ncode,\nform,\nfieldset,\nlegend,\ninput,\ntextarea,\np,\nblockquote,\nth,\ntd,\nhr,\nbutton,\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection,\n* {\n    margin: 0;\n    padding: 0;\n}\n\n\n/* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */\n\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n    display: block;\n}\n\n\n/* HTML5 媒体文件跟 img 保持一致 */\n\naudio,\ncanvas,\nvideo {\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n}\n\n\n/* 要注意表单元素并不继承父级 font 的问题 */\n\nbody,\nbutton,\ninput,\nselect,\ntextarea {\n    font-style: normal;\n    font-size: $fontSizeBase;\n    font-family: \\5FAE\\8F6F\\96C5\\9ED1, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n    color: $font-baseColor;\n}\n\ninput,\nselect,\ntextarea {\n    font-size: 100%;\n}\n\n\n/* 去掉各Table  cell 的边距并让其边重合 */\n\ntable {\n    border-collapse: collapse;\n    border-spacing: 0;\n}\n\n\n/* IE bug fixed: th 不继承 text-align*/\n\nth {\n    text-align: inherit;\n}\n\n\n/* 去除默认边框 */\n\nfieldset,\nimg {\n    border: 0;\n}\n\n\n/* ie6 7 8(q) bug 显示为行内表现 */\n\niframe {\n    display: block;\n}\n\n\n/* 去掉 firefox 下此元素的边框 */\n\nabbr,\nacronym {\n    border: 0;\n    font-variant: normal;\n}\n\n\n/* 一致的 del 样式 */\n\ndel {\n    text-decoration: line-through;\n}\n\naddress,\ncaption,\ncite,\ncode,\ndfn,\nem,\nth,\nvar {\n    font-style: normal;\n    font-weight: 500;\n}\n\n\n/* 去掉列表前的标识, li 会继承 */\n\nol,\nul {\n    list-style: none;\n}\n\n\n/* 对齐是排版最重要的因素, 别让什么都居中 */\n\ncaption,\nth {\n    text-align: left;\n}\n\n\n/* 来自yahoo, 让标题都自定义, 适应多个系统应用 */\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font-size: 100%;\n    font-weight: 500;\n}\n\nq:before,\nq:after {\n    content: '';\n}\n\n\n/* 统一上标和下标 */\n\nsub,\nsup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n}\n\nsup {\n    top: -0.5em;\n}\n\nsub {\n    bottom: -0.25em;\n}\n\n\n/* 正常链接 未访问 */\n\na:link {\n    text-decoration: none;\n}\n\n\n/* 鼠标悬停 */\n\na:hover {\n    text-decoration: none;\n}\n\n\n/* 默认不显示下划线，保持页面简洁 */\n\nins,\na {\n    text-decoration: none;\n}\n\n\n/* 代码字体 */\n\ncode,\nkbd,\npre,\nsamp {\n    font-family: monospace, serif;\n    font-size: 1em;\n}\n\n\n/* 隐藏, 通常用来与 JS 配合 */\n\nbody .fn-hide {\n    display: none;\n}\n\n\n/* a链接去掉点击时候的虚线*/\n\na,\nbutton,\ninput {\n    text-decoration: none;\n    outline: none;\n    /*ff*/\n    // hide-focus: expression(this.hideFocus=true);\n    /*ie*/\n}\n\na:hover {\n    text-decoration: none;\n}\n\n\n/*初始化系统默认样式*/\n\ni,\nem {\n    font-style: normal;\n}\n\ninput,\nselect,\ntextarea {\n    vertical-align: middle;\n    -webkit-appearance: none;\n    border-radius: 0;\n    outline: none;\n    font-family: \\5FAE\\8F6F\\96C5\\9ED1, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n    color: #333;\n}\n\n*,\n*:after,\n*::before {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n}\n\n\n/* 清除浏览器默认行为 */\n\n* {\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n    -webkit-touch-callout: none;\n}\n\na,\nbutton {\n    -webkit-user-select: none;\n}\n\ninput[type=tel],\ninput[type=number],\ninput[type=password],\ninput[type=date] {\n    font-family: tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n}\n\n::-webkit-input-placeholder {\n    color: #999;\n    opacity: 1;\n}\n\n:-moz-placeholder {\n    color: #999;\n    opacity: 1;\n}\n\n::-moz-placeholder {\n    color: #999;\n    opacity: 1;\n}\n\n:-ms-input-placeholder {\n    color: #999;\n    opacity: 1;\n}\n\ninput::-webkit-input-placeholder {\n    color: #c7c7c7;\n}\n\ninput::-moz-placeholder {\n    /* Mozilla Firefox 19+ */\n    color: #c7c7c7;\n}\n\ninput:-moz-placeholder {\n    /* Mozilla Firefox 4 to 18 */\n    color: #c7c7c7;\n}\n\ninput:-ms-input-placeholder {\n    /* Internet Explorer 10-11 */\n    color: #c7c7c7;\n}\n\n\n/*input[type=\"date\"]:before {\n    content: attr(placeholder);\n    color:#c7c7c7;width: 100%;\n    text-align: center;\n}*/\n\nbody {\n    color: #000;\n    margin: 0 auto;\n    overflow-x: hidden;\n    // width: 7.5rem;\n    min-height: 100%;\n    background: #ebebeb;\n    font-family: 'Avenir', '微软雅黑', '黑体', Helvetica, sans-serif;\n    font-size: $fontSizeBase;\n}\n\na {\n    color: #666;\n}\n\n\n/*------------global-css-------------*/\n\n\n/* 清理浮动 */\n\n.fn-clear:after {\n    visibility: hidden;\n    display: block;\n    font-size: 0;\n    content: '';\n    clear: both;\n    height: 0;\n}\n\n.fn-clear {\n    zoom: 1;\n    /* for IE6 IE7 */\n}\n\n\n/*公共间距*/\n\n.mt10 {\n    margin-top: .1rem;\n}\n\n.mt20 {\n    margin-top: .2rem;\n}\n\n.mb10 {\n    margin-bottom: .1rem;\n}\n\n.mb20 {\n    margin-bottom: .2rem;\n}\n\n.ml10 {\n    margin-left: .1rem;\n}\n\n.ml20 {\n    margin-left: .2rem;\n}\n\n\n/*浮动*/\n\n.left {\n    float: left;\n}\n\n.right {\n    float: right;\n}\n\n.heightFull {\n    min-height: 100%;\n    background: #ebebeb\n}\n\n.contaniner {\n    position: relative;\n    min-height: 100%;\n    &>.main {\n        min-height: 100%;\n        padding-top: .88rem;\n        padding-bottom: 1.3rem;\n    }\n}\n\n.com-padding {\n    padding: 0 .33rem;\n}\n\n.com-content {\n    background: #fff;\n}\n\n.arrow {\n    position: absolute;\n    right: .25rem;\n    top: 0;\n    i {\n        display: block;\n        color: #bbb;\n        -webkit-transform: rotate(-90deg);\n        transform: rotate(-90deg);\n    }\n}\n\n.base-select {\n    // width: 2.2rem;\n    line-height: .45rem;\n    position: relative;\n    border: 1px solid #cad3df;\n    border-radius: 3px;\n    padding-right: 20px;\n    span {\n        padding: .25rem 0;\n        width: 100%;\n        height: .5rem;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n        overflow: hidden;\n        padding-left: .15rem;\n    }\n    .arrow {\n        right: .1rem;\n    }\n}\n\n.base-bd {\n    border: 1px solid $border-gray;\n}\n\n\n/*------没有边框的选择框-----*/\n\n.no-bd-select {\n    width: 100%;\n    text-align: center;\n    span {\n        display: inline-block;\n        color: $font-blackColor;\n 